//FONTS
@font-face
    font-family: 'Maison Neue'
    src: url('fonts/MaisonNeue-Light.woff2') format('woff2')
    font-weight: 300
    font-style: normal
@font-face
    font-family: 'Maison Neue'
    src: url('fonts/MaisonNeue-Medium.woff2') format('woff2')
    font-weight: 500
    font-style: normal
@font-face
    font-family: 'Maison Neue'
    src: url('fonts/MaisonNeue-Bold.woff2') format('woff2'),
    font-weight: bold
    font-style: normal
@font-face
    font-family: 'Press Start 2P'
    src: url('fonts/PressStart2P-Regular.woff2') format('woff2'),
    font-weight: normal


*
    margin: 0
    padding: 0
    outline: 0 none
    box-sizing: border-box
    font-family: 'Maison Neue', sans-serif

html
body

.nav
    display: flex
    position: fixed
    top: 0
    left: 0
    right: 0
    padding: 20px
    justify-content: space-between
    align-items: center

.name
    font-size: 15px
    letter-spacing: 1px
    font-weight: bold
    text-transform: uppercase
    font-family: 'Press Start 2P'
    // position: fixed
    // left: 20px
    // top: 20px
.links
    display: flex
    align-items: center
    justify-content: center
    margin: 0 0 0 30px

    gap: 20px
    li
        list-style: none
        display: block
        width: 32px
        height: 32px

        a
            display: block
            width: 100%
            height: 100%
            background: #333
            //border-radius: 100%
            font-size: 0
            &:hover
                opacity: 0.5
        &.gh
            a
                background: url(../imgs/github.svg) no-repeat center center
                background-size: contain
        &.so
            a
                background: url(../imgs/stackoverflow.png) no-repeat center center
                background-size: contain
.list
    margin: 0
    list-style: none
    display: flex
    flex-direction: column
    gap: 10px 0
    li
        font-weight: bold
        font-size: 25px

.wrapper
    margin: 0 auto
    max-width: 400px
    width: 100%
    display: flex
    flex-direction: column
    justify-content: center
    align-items: flex-start

    position: fixed
    inset: 0
    gap: 0

.title
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: flex-start
    gap: 15px
    margin-bottom: 0
    position: relative
    animation: hover 6s ease-in-out infinite
    &.stop
        animation: none



.iconWrapper
    display: flex
    align-items: center
    gap: 10px
.icon
    margin-left: 30px
    display: block
    width: 50px
    height: 50px
    background: url(imgs/capa-talking.gif) no-repeat center center transparent
    background-size: contain
    //animation: hover 4s ease-in-out infinite

.desc
    display: flex
    flex-direction: column
    align-items: flex-start
    padding-left: 65px
    gap: 10px
    h2
        font-size: 30px
        color: #999
        font-weight: 500
        letter-spacing: -0.3px
    p
        font-size: 19px
        font-weight: 300
        line-height: 130%



.bubble
    position: relative
    display: inline-block
    margin: 20px
    text-align: left

    background-color: #fff
    color: #000

    box-shadow: 0 -4px #fff, 0 -8px #000, 4px 0 #fff, 4px -4px #000, 8px 0 #000, 0 4px #fff, 0 8px #000, -4px 0 #fff, -4px 4px #000, -8px 0 #000, -4px -4px #000, 4px 4px #000
    box-sizing: border-box
    width: 200px
    height: 184px
    p
        height: 172px
        overflow-y: auto
        font-family: "Press Start 2P", cursive
        font-size: 16px
        line-height: 2em
        padding: 5px 12px

    &::after
        content: ""
        display: block
        position: absolute
        box-sizing: border-box
    &.medium
        width: 350px
    &.bottom::after
        height: 4px
        width: 4px
        bottom: -8px
        left: 32px
        box-shadow: 0 4px #000, 0 8px #000, 0 12px #000, 0 16px #000, -4px 12px #000, -8px 8px #000, -12px 4px #000, -4px 4px #fff, -8px 4px #fff, -4px 8px #fff, -4px 0 #fff, -8px 0 #fff, -12px 0 #fff
    &.left::after
        height: 4px
        width: 4px
        top: 20px
        left: -8px
        background: white
        box-shadow: -4px -4px #fff, -4px 0 #fff, -8px 0 #fff, 0 -8px #fff, -4px 4px #000, -8px 4px #000, -12px 4px #000, -16px 4px #000, -12px 0 #000, -8px -4px #000, -4px -8px #000, 0 -4px #fff

.choices
    position: fixed
    left: 0
    bottom: 0
    right: 0
    width: 100%
    display: flex
    align-items: center
    justify-content: center
    padding: 40px
    gap: 40px


    button
        padding: 5px 10px
        display: block
        width: max-content
        background-color: #fff
        color: #000
        border: none
        outline: none
        font-family: "Press Start 2P", cursive
        font-size: 16px
        line-height: 100%
        box-shadow: 0 -4px #fff, 0 -8px #000, 4px 0 #fff, 4px -4px #000, 8px 0 #000, 0 4px #fff, 0 8px #000, -4px 0 #fff, -4px 4px #000, -8px 0 #000, -4px -4px #000, 4px 4px #000
        &:hover
            cursor: pointer



/* Custom scrollbar styles */
::-webkit-scrollbar,
::-webkit-scrollbar-track

    background-color: #fff
    width: 8px
::-webkit-scrollbar-thumb
    background-color: #000 !important







@keyframes hover
    0%, 100%
        transform: translateY(0)
    50%
        transform: translateY(10%)


@media screen
    @media (max-width: 500px)
        .wrapper
            padding: 0 30px
            align-items: center
        .title
            flex-direction: column
            align-items: center
            .icon
                // width: 30px
                // height: 30px
            .name
                font-size: 40px
                text-align: center

        .desc
            padding-left: 0
            align-items: center
            h2
                font-size: 22px
            p
                font-size: 16px
                text-align: center

        .list
            margin-left: 0
            li
                text-align: center

